        </el-col>
        <el-col :span="8" class="input-item">
          <span class="input-text"></span>
          <span class="input-input detailinput">  {{mhinfo.CREATE_DATE}}</span>
        </el-col>
    </el-row>

</el-form>
</template>
<script>
export default {
  name:'MHXX',
  props:['type','xid'],
  data(){
    return{
      mhinfo:{},
      page:this.type,
      id:this.xid,
      pp:{},
    }
  },
  mounted(){
      this.initData();
   },
  watch:{
      type: function(val){
        this.page=val;
      },
      xid:{
        handler(val){
        this.id=val;
        this.initData()
      },
      immediate: true
      },
    },

  methods:{
    initData(){
      switch (this.page) {
        case 2://
            this.getData2();
            break;
        default:
      }
    },

    getData2(){
      this.pp.RGUID=this.id;
      let p = {
        "pd": this.pp
      };
       this.$api.post(this.Global.aport3+'/ryhx/getstmhjgxxn', p,
        r => {

          if(r.data.resultList.length){
          this.mhinfo=r.data.resultList[0];
         }
      })
    },
  },
}
</script>
<style scoped>
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

.el-carousel__item img {
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.el-carousel__item:nth-child(2n) {
  /* background: url(../../../assets/img/t1.png); */
  background-size: 100% 100%;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}

.crcolor {
  background: #EFF3F6;padding:0 10px;
}
.input-text {
  text-align: left !important; width: 30%!important;
}
.input-input {
  width: 60%!important;
}
</style>
<style>
  .el-button+.el-button{margin-left: 0!important;}
  .yycontent .el-checkbox{margin-left: 20px!important; line-height: 30px;}
  .yycontent .el-checkbox+.el-checkbox{margin-left: 20px!important;}
  .bj .el-dialog__wrapper {
    background: #000;
    background: rgba(0, 0, 0, 0.3);
  }
</style>



